@import url(https://fonts.googleapis.com/css2?family=Montserrat);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-appearance: none;
	overflow: hidden;
	height: 100%;
}

@media (max-width: 800px) {
	body {
		overflow: auto;
	}
	#tramitar{
		font-size: 12px;
		width: 100%;
		margin-bottom: 20px;
	}
	#trash{
		font-size: 12px;
		display: inline;
		margin-right: 30%;
		
		
	}
}

@media (max-width: 300px) {
	body {
		overflow: auto;
	}
	#tramitar{
		font-size: 10px;
		width: 100%;
		margin-bottom: 20px;
	}
	#trash{
		font-size: 10px;
		display: inline;
		margin-right: 30%;
		
		
	}
}

html::-webkit-scrollbar {
	-webkit-appearance: none;
}


.context {
	width: 49%;
	display: inline-block;
	height: 90vh;
	margin-left: 10px;
	-webkit-appearance: none;
}

#context2::-webkit-scrollbar {
	-webkit-appearance: none;
}

#zoom {
	border: none;
	border-radius: 50%;

	padding: none;
}

hr {
	color: black;
	display: block;
	margin: 10px 0;
	line-height: 500%;
}

#move {
	position: absolute;
	z-index: 155555;
}

#tramitar {
	margin-right:50%;

	padding: none;
}

#zoomMinus {
	border: none;
	border-radius: 50%;

	padding: none;
}
#zoom:hover {
	border: none;
	border-radius: 50%;

	background-color: none;
	padding: none;
	color: none;
}

#img {
	display: block;
	margin-top: 10%;
	margin-left: 25%;
	padding: 0;
}

@media (max-width: 1500px) {
	#img {
		margin-top: 5px;
		margin-left: 5px;
	}
}

@media (max-width: 600px) {
	#img {
		margin: auto;
	}
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  text-align: center;  
  background-color:white;
  overflow-x: hidden;
  
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

img.menu_links { cursor: pointer; }

